import PropTypes from 'prop-types';
import React from 'react';

const EventInfoTag = ({tag}) => {
    const onTagClick = window.alert;

    return (
        <span className="event-info__tag" onClick={onTagClick}>{tag}</span>
    );
};

EventInfoTag.propTypes = {
    tag: PropTypes.string.isRequired
};

export default EventInfoTag;



