import PropTypes from 'prop-types';
import React from 'react';
import ReactMarkdown from 'react-markdown';

import Header from './__header/event-info__header';

const EventInfo = ({description, duration, location, tags, times, title}) => (
    <div className="event-info">
        <Header {...{location, tags, times, title}} />
        <ReactMarkdown className="event-info__description" source={description} />
    </div>
);

EventInfo.propTypes = {
    description: PropTypes.string.isRequired,
    duration: PropTypes.number,
    location: PropTypes.string,
    tags: PropTypes.arrayOf(PropTypes.string).isRequired,
    times: PropTypes.arrayOf(PropTypes.string).isRequired,
    title: PropTypes.string.isRequired
}

export default EventInfo;



