import PropTypes from 'prop-types';
import React from 'react';

import { FOR_CHILDREN_TAG } from '../../../constants';

import './timetable__event.css';

const TimetableEvent = ({event, timeSlot}) => {
    const {title, tags} = event;
    const isForChildren = tags.includes(FOR_CHILDREN_TAG);

    return (
        <div className="timetable__event">
            <div className="timetable__event-header">
                <span className="timetable__event-time">{timeSlot}</span>
                {isForChildren &&
                    <i className="timetable__event-for-children-icon" />}
            </div>
            <h3 className="timetable__event-title">{title}</h3>
        </div>
    );
};

TimetableEvent.propTypes = {
    event: PropTypes.object.isRequired,
    timeSlot: PropTypes.string.isRequired
};

export default TimetableEvent;



